.footer {
  align-self: end;
}

.footer-img {
  height: 64px;
  width: 64px;
  background-color: rgba(59, 187, 255, 0);
  border-radius: 5%;
  box-shadow: 10px 10px;
}

.footer-list {
  display: flex;
  list-style-type: none;
  width: 100%;
  justify-content: space-evenly;
}

.footer-item {
  padding: 10px;
}
